<template><section><h1>CustomDataSource</h1>
<p><code v-pre>vc-datasource-custom</code> 组件用于加载自定义数据源，手动管理一组实体。</p>
<h2>示例</h2>
<h3>加载 Custom 数据源</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="onViewerReady">
        <vc-datasource-custom name="custom" :entities="entities" @click="onClicked" :show="show">
          <vc-entity ref="entity1" @click="onClicked" :position="[108, 35, 100]" :billboard="{
              image: 'https://zouyaoji.top/vue-cesium/favicon.png',
              show: true,
              pixelOffset: [0, -20],
              eyeOffset: {x: 0, y: 0, z: 0},
              horizontalOrigin: 0,
              verticalOrigin: 1,
              scale: 0.25,
              color: 'lime'
            }" description="Hello Vue Cesium" id="This is a billboard">
          </vc-entity>
          <vc-entity ref="entity2" :position="[105,40,200000]" description="Hello Vue Cesium">
            <vc-graphics-cylinder ref="cylinder1" :length="400000.0" :topRadius="200000.0" :bottomRadius="200000.0" :material="[0,255,0,125]" :outline="true" outlineColor="#006400"></vc-graphics-cylinder>
          </vc-entity>
        </vc-datasource-custom>
        <vc-datasource-custom ref="datasourceRef" @click="onClicked" :key="index" :show="show" :name="datasource.name" v-for="(datasource, index) of datasources" :entities="datasource.entities" @clusterEvent="onDatasourceClusterEvent" @ready="onDatasourceReady">
        </vc-datasource-custom>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
