            <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"getPosition(item)"</span> <span class="hljs-attr">:ref</span>=<span class="hljs-string">"'entity' + index"</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-billboard</span>
