<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">id</span>: <span class="hljs-string">'This is a billboard'</span>,
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>,
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">108</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">100</span> },
        <span class="hljs-attr">billboard</span>: {},
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">cylinder1</span>: {},
        <span class="hljs-attr">position1</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">105.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">200000.0</span> },
        <span class="hljs-attr">outlineColor1</span>: <span class="hljs-string">'DARK_GREEN'</span>,
        <span class="hljs-attr">material1</span>: {},

        <span class="hljs-attr">cylinder2</span>: {},
        <span class="hljs-attr">position2</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">110.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">200000.0</span> },
        <span class="hljs-attr">material2</span>: <span class="hljs-string">'RED'</span>,

        <span class="hljs-attr">datas</span>: [],
        <span class="hljs-attr">entities</span>: [
          {
            <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">105</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">200</span> },
            <span class="hljs-attr">point</span>: {
              <span class="hljs-attr">pixelSize</span>: <span class="hljs-number">5</span>,
              <span class="hljs-attr">color</span>: <span class="hljs-string">'red'</span>
            }
          },
          {
            <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">105</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">36</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300</span> },
            <span class="hljs-attr">point</span>: {
              <span class="hljs-attr">pixelSize</span>: <span class="hljs-number">8</span>,
              <span class="hljs-attr">color</span>: <span class="hljs-string">'yellow'</span>
            }
          }
        ]
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([
        <span class="hljs-keyword">this</span>.$refs.entity1.createPromise,
        <span class="hljs-keyword">this</span>.$refs.cylinder1.createPromise,
        <span class="hljs-keyword">this</span>.$refs.cylinder2.createPromise
      ]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        <span class="hljs-comment">// instances[0].viewer.zoomTo(this.$refs.datasource.cesiumObject)</span>
        instances[<span class="hljs-number">0</span>].viewer.camera.setView({
          <span class="hljs-attr">destination</span>: <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">-2310285.0191093646</span>, <span class="hljs-number">5365872.967043371</span>, <span class="hljs-number">3108924.304301176</span>),
          <span class="hljs-attr">orientation</span>: {
            <span class="hljs-attr">heading</span>: <span class="hljs-number">0.07310634629277768</span>,
            <span class="hljs-attr">pitch</span>: <span class="hljs-number">-1.5094668006074268</span>,
            <span class="hljs-attr">roll</span>: <span class="hljs-number">0.0003451814958399524</span>
          }
        })
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.material1 = Cesium.Color.GREEN.withAlpha(<span class="hljs-number">0.5</span>)
        <span class="hljs-keyword">this</span>.billboard = <span class="hljs-keyword">new</span> Cesium.BillboardGraphics({
          <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>, <span class="hljs-comment">// default: undefined</span>
          <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>, <span class="hljs-comment">// default</span>
          <span class="hljs-attr">pixelOffset</span>: <span class="hljs-keyword">new</span> Cesium.Cartesian2(<span class="hljs-number">0</span>, <span class="hljs-number">-50</span>), <span class="hljs-comment">// default: (0, 0)</span>
          <span class="hljs-attr">eyeOffset</span>: <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">0.0</span>, <span class="hljs-number">0.0</span>, <span class="hljs-number">0.0</span>), <span class="hljs-comment">// default</span>
          <span class="hljs-attr">horizontalOrigin</span>: Cesium.HorizontalOrigin.CENTER, <span class="hljs-comment">// default</span>
          <span class="hljs-attr">verticalOrigin</span>: Cesium.VerticalOrigin.BOTTOM, <span class="hljs-comment">// default: CENTER</span>
          <span class="hljs-attr">scale</span>: <span class="hljs-number">0.5</span>, <span class="hljs-comment">// default: 1.0</span>
          <span class="hljs-attr">color</span>: Cesium.Color.LIME, <span class="hljs-comment">// default: WHITE</span>
          <span class="hljs-comment">// rotation: Cesium.Math.PI_OVER_FOUR, // default: 0.0</span>
          <span class="hljs-attr">alignedAxis</span>: Cesium.Cartesian3.ZERO <span class="hljs-comment">// default</span>
        })
        <span class="hljs-keyword">this</span>.addPoints(<span class="hljs-literal">true</span>)
      },
      datasourceReady(instance) {
        <span class="hljs-keyword">let</span> { Cesium, viewer, cesiumObject } = instance
        <span class="hljs-keyword">const</span> that = <span class="hljs-keyword">this</span>
        <span class="hljs-keyword">this</span>.$refs[<span class="hljs-string">'ds'</span> + instance.name][<span class="hljs-number">0</span>].createPromise.then(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer, cesiumObject }</span>) =&gt;</span> {
          <span class="hljs-comment">//开启聚合功能</span>
          cesiumObject.clustering.enabled = <span class="hljs-literal">true</span>
          cesiumObject.clustering.pixelRange = <span class="hljs-number">30</span>
          cesiumObject.clustering.minimumClusterSize = <span class="hljs-number">3</span>
        })
      },
      datasourceClusterEvent(clusteredEntities, cluster) {
        cluster.label.show = <span class="hljs-literal">true</span>
        cluster.label.scale = <span class="hljs-number">0.5</span>
        cluster.label.fillColor = Cesium.Color.fromCssColorString(<span class="hljs-string">'#FFFFFF'</span>)
        cluster.label.style = Cesium.LabelStyle.FILL
        cluster.label.font = <span class="hljs-string">'30px caption'</span>
        cluster.label.VerticalOrigin = Cesium.VerticalOrigin.BOTTOM
        cluster.label.pixelOffset = <span class="hljs-keyword">new</span> Cesium.Cartesian2(<span class="hljs-number">-7</span>, <span class="hljs-number">5</span>)
        cluster.label.disableDepthTestDistance = <span class="hljs-built_in">Number</span>.POSITIVE_INFINITY
        cluster.point.show = <span class="hljs-literal">true</span>
        <span class="hljs-comment">//聚合体属性</span>
        cluster.point.id = cluster.label.id
        cluster.point.color = Cesium.Color.LIGHTSLATEGRAY
        <span class="hljs-keyword">if</span> (clusteredEntities.length &gt;= <span class="hljs-number">100</span>) {
          cluster.point.pixelSize = <span class="hljs-number">38</span>
          cluster.label.pixelOffset = <span class="hljs-keyword">new</span> Cesium.Cartesian2(<span class="hljs-number">-12</span>, <span class="hljs-number">5</span>)
        } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (clusteredEntities.length &gt;= <span class="hljs-number">50</span>) {
          cluster.point.pixelSize = <span class="hljs-number">36</span>
        } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (clusteredEntities.length &gt;= <span class="hljs-number">40</span>) {
          cluster.point.pixelSize = <span class="hljs-number">33</span>
        } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (clusteredEntities.length &gt;= <span class="hljs-number">30</span>) {
          cluster.point.pixelSize = <span class="hljs-number">28</span>
        } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (clusteredEntities.length &gt;= <span class="hljs-number">20</span>) {
          cluster.point.pixelSize = <span class="hljs-number">25</span>
        } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (clusteredEntities.length &gt;= <span class="hljs-number">10</span>) {
          cluster.label.pixelOffset = <span class="hljs-keyword">new</span> Cesium.Cartesian2(<span class="hljs-number">-6</span>, <span class="hljs-number">4</span>)
          cluster.label.scale = <span class="hljs-number">0.4</span>
          cluster.point.pixelSize = <span class="hljs-number">20</span>
        } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (clusteredEntities.length &gt;= <span class="hljs-number">5</span>) {
          cluster.label.pixelOffset = <span class="hljs-keyword">new</span> Cesium.Cartesian2(<span class="hljs-number">-3</span>, <span class="hljs-number">4</span>)
          cluster.label.scale = <span class="hljs-number">0.4</span>
          cluster.point.pixelSize = <span class="hljs-number">15</span>
        } <span class="hljs-keyword">else</span> {
          cluster.label.pixelOffset = <span class="hljs-keyword">new</span> Cesium.Cartesian2(<span class="hljs-number">-3</span>, <span class="hljs-number">4</span>)
          cluster.label.scale = <span class="hljs-number">0.4</span>
          cluster.point.pixelSize = <span class="hljs-number">13</span>
        }
      },
      getPosition(item) {
        <span class="hljs-keyword">return</span> {
          <span class="hljs-attr">lng</span>: <span class="hljs-built_in">Number</span>(item.Longitude),
          <span class="hljs-attr">lat</span>: <span class="hljs-built_in">Number</span>(item.Latitude),
          <span class="hljs-attr">height</span>: <span class="hljs-number">1000</span>
        }
      },
      addPoints(flag) {
        <span class="hljs-keyword">const</span> item = {
          <span class="hljs-attr">id</span>: <span class="hljs-string">'1001'</span>,
          <span class="hljs-attr">code</span>: <span class="hljs-string">'1001'</span>,
          <span class="hljs-attr">name</span>: <span class="hljs-string">'test'</span>,
          <span class="hljs-attr">iconOn</span>: <span class="hljs-string">'./statics/SampleData/points/pic.png'</span>,
          <span class="hljs-attr">giscolor</span>: <span class="hljs-string">'#fb7228'</span>,
          <span class="hljs-attr">datauri</span>: <span class="hljs-string">'./statics/SampleData/points/yj.json'</span>
        }
        <span class="hljs-keyword">this</span>.showPoints(item, flag)
      },
      showPoints(item, flag) {
        <span class="hljs-keyword">const</span> that = <span class="hljs-keyword">this</span>
        <span class="hljs-keyword">if</span> (flag) {
          Cesium.Resource.fetchJson(item.datauri).then(<span class="hljs-function">(<span class="hljs-params">res</span>) =&gt;</span> {
            <span class="hljs-keyword">const</span> img = <span class="hljs-keyword">new</span> Image()
            img.src = item.iconOn
            img.onload = <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
              <span class="hljs-keyword">let</span> datas = []
              <span class="hljs-keyword">let</span> data = {
                <span class="hljs-attr">name</span>: item.code,
                <span class="hljs-attr">type</span>: <span class="hljs-string">'point'</span>,
                <span class="hljs-attr">clustering</span>: <span class="hljs-literal">true</span>,
                <span class="hljs-attr">data</span>: [],
                <span class="hljs-attr">iconUrl</span>: item.iconOn,
                <span class="hljs-attr">color</span>: item.giscolor,
                <span class="hljs-attr">width</span>: img.width,
                <span class="hljs-attr">height</span>: img.height
              }
              data.data = res.reduce(<span class="hljs-function">(<span class="hljs-params">pre, cur</span>) =&gt;</span> {
                <span class="hljs-keyword">return</span> pre.concat({
                  <span class="hljs-attr">Longitude</span>: cur.Longitude,
                  <span class="hljs-attr">Latitude</span>: cur.Latitude,
                  <span class="hljs-attr">id</span>: cur.id,
                  <span class="hljs-attr">description</span>: cur.name
                })
              }, [])

              datas.push(data)
              that.datas = datas
            }
          })
        } <span class="hljs-keyword">else</span> {
          <span class="hljs-keyword">this</span>.datas = []
        }
      },
      clicked(e) {
        <span class="hljs-built_in">console</span>.log(e)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> 指定数据源名字。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定数据源是否显示。</td>
</tr>
<tr>
<td>entities</td>
<td>Array</td>
<td><code v-pre>[]</code></td>
<td><code v-pre>optional</code> 指定要添加到该数据源的实体集合。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CustomDataSource.html">CustomDataSource</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>changedEvent</td>
<td></td>
<td>数据源改变时触发。</td>
</tr>
<tr>
<td>errorEvent</td>
<td></td>
<td>数据源发生错误时触发。</td>
</tr>
<tr>
<td>loadingEvent</td>
<td></td>
<td>数据源开始或结束加载时触发。</td>
